<template>
	<div>
    <swipe class="swiper_home" :speed="600" >
        <swipe-item class="slide1" v-for="banner in homeData.topimage_ary"> <img :src="banner" alt=""></swipe-item>
      </swipe>
    <div class="inner bar-title dpr-bottom">
      <h1>{{homeData.user_name}}</h1>
      <p></p>
    </div>
		<h1>home</h1>
		<router-link :to="{ name: 'trip'}">to list</router-link>
		<div class="demo px-t">
      asd  
    </div>
    <div class="demo px-b">
      asd  
    </div>
    <br>
    <div class="demo px-tb">
      asd  
    </div>
    <div class="demo px-l">
      asd  
    </div>
    <div class="demo px-r">
      asd  
    </div>
    <div class="demos px-all" style="margin: 20px">
      sd
    </div>
    <div class="flexbox">
        <div class="flex">1</div>
        <div class="flex">2</div>
    </div>
    <nav-bar></nav-bar>
	</div>

</template>

<script>
  import {mapState, mapGetters} from 'vuex';
  import Toast from 'vue-toast-mobile';
  import navBar from '../components/navBar.vue';
  import { Swipe, SwipeItem } from 'vue-swipe';

  export default {
  	data () {
  		return {
        homeData: {}
  		}
  	},
    computed: {
      ...mapGetters({
        userInfo: 'getUserInfo'
      })
    },
  	methods: {
  		async getContent () {
	        const response = await fetch(this.$appConfig.api.home + '&member_id=' + this.userInfo.member_id);
	        this.homeData = (await response.json()).datas;
          this.$store.commit('UPDATE_LOADING', false);
	      }
  	},
  	mounted () {
  		this.getContent();
  	},
    components: {
      navBar,
      Swipe,
      SwipeItem
    }
  }	
</script>
<style lang="scss">
@import "../../node_modules/vue-swipe/dist/vue-swipe.css";
@import '../assets/scss/style.scss';
  .swiper_home {
    height: rem(400px);
    position: relative;
    overflow: hidden;
  }
  .bar-title{
    padding-bottom: 1px;
    height: 65px;
    h1{
      margin-bottom:0;
      height: 42px;
    }
    p{
      margin-top:0;
    }
  }
</style>


